import React, { Suspense } from "react";
import Loader from "../components/Loader";
import Author from "./corporate/author";
import About from "./corporate/about";
import Contact from "./corporate/contact";
import Navigation from "../components/navigation";
import { useSnapshot } from "valtio";
import state from "../store";
const Corporate = () => {
  const snap = useSnapshot(state);
  return (
    <Suspense fallback={<Loader />}>
      <div className="corporate perspect-1 inline-flex relative flex-col justify-center items-start z-10 m-0">
        <section className="backdrop-blur-md p-0 xl:p-4 bg-white/80 relative">
          <Author id="home" />
          <About id="about" />
          <Contact id="contact" />
          <img
            className="fixed w-full bottom-0 left-full scale-0 xl:scale-125"
            src={snap.background}
            alt="Plane flying"
            title="Off to space"
          />
          <img
            className="absolute w-[130px] top-0 right-full scale-0 xl:scale-125 -rotate-45"
            src={snap.satellite}
            alt="Plane flying"
            title="Off to space"
            onClick={() => {}}
          />
        </section>
        <div className="hidden xl:inline-block">
          <Navigation />
        </div>
      </div>
    </Suspense>
  );
};

export default Corporate;
