import React from "react";
import jsonData from "../../json/data.json";
const Author = ({ ...props }) => {
  return (
    <div className="w-full flex flex-col relative lg:min-h-[600px] my-10 max-w-7xl" {...props}>
      <div className=" max-w-full xl:max-w-6xl z-20 w-full ">
        <p className="text-4xl sm:text-5xl xl:text-7xl z-10 mb-10">
          <span className="flex px-4 xl:p-0">
            <span className="inline-flex Anuphan design">Work</span>
            <span className="ml-3 Alfa text-gradB design">History</span>
          </span>
        </p>
      </div>
      <ul className="inline-flex w-full flex-wrap rounded-xl">
          {jsonData.work_history.map(
            ({ company, title, duration }, index) => (
              <li
                className={` bottom-0 transition-all w-full max-w-sm p-4 z-0 `}
                key={index}
              >
                <p className="flex w-full h-full">
                  <span className="flex flex-col gap-2 w-full flex-1 h-full">
                    <span className="text-xl">{company}</span>
                    <span className="text-lg">{title}</span>
                    <span className="text-sm">{duration}</span>
                  </span>
                </p>
              </li>
            )
          )}
        </ul>
    </div>
  );
};

export default Author;
