import React from "react";
import { HiHomeModern } from "react-icons/hi2";
import jsonData from "../json/data.json";
import TransitionLink from "../hooks/TransitionLink";
const Navigation = () => {
  const iconOptions = {
    size: "48px",
  };
  return (
    <>
      <nav className="navigation flex flex-col fixed z-40 left-0 min-h-screen top-0 items-center ">
        <TransitionLink to="/" className=" text-amber-400 flex items-center justify-center ">
          <HiHomeModern {...iconOptions} />
        </TransitionLink>
        <h1 className="text-center flex-1 portfolio-title backdrop-blur-sm z-20 relative nav-background transition-all ">
          {jsonData.logo}
        </h1>
      </nav>
    
    </>
  );
};
export default Navigation;
