
import { GrLinkedin, GrCodepen } from "react-icons/gr";
export default function Contactbar({size}) {
  return (
    <div className="inline-flex gap-4 z-[50] justify-center mb-10 xl:mb-0">
      <a
        rel="noreferrer"
        href="https://www.linkedin.com/in/david-grant-2b3295b3/#ember118"
        target="_blank"
        className="inline-block"
      >
        <GrLinkedin className={`${size?'iconSm':'iconLg'}`} />
      </a>
      <a
        rel="noreferrer"
        href="https://codepen.io/drgrant"
        target="_blank"
        className="inline-block"
      >
        <GrCodepen className={`${size?'iconSm':'iconLg'} rounded-full`} />
      </a>
   
    </div>
  );
}
