import React from "react";
import jsonData from "../../json/data.json";
import { useSnapshot } from "valtio";
import state from "../../store";
const Author = ({ ...props }) => {
  const snap = useSnapshot(state);
  return (
    <div
      className="w-full flex relative lg:min-h-[600px] max-w-7xl overflow-hidden"
      {...props}
    >
      <div className="  Anuphan w-full flex justify-center items-center flex-col md:flex-row backdrop-blur-xl">
        <section className="flex-1 flex  w-full h-full relative z-0 justify-center items-center">
          <img
            className=" w-full h-full p-0 object-cover object-center saturate-50 blur-[1px] relative"
            src={snap.authorsPicOlder}
            alt="author portrait"
            title="picture of author"
          />
        </section>
        <section className="Anuphan w-full max-w-xl flex flex-col items-center justify-center p-2 relative sm:p-20 bg-white">
          <p className="mb-0 lg:mb-6 z-10 relative">
            <span className="flex items-center flex-col">
              <span className="text-3xl sm:text-4xl grad-t-1">
                {props?.title || jsonData.title}
              </span>
              <span className="text-3xl sm:text-7xl grad-t-2 Alfa mb-6 text-center">
                {jsonData.name}
              </span>
            </span>
          </p>
          <p className="leading-6 text-lg z-[5] relative mb-10 ">
            <span className="inline-block p-3">
              {jsonData.personal_info.map((text, index) => (
                <span key={index} className="inline ml-2">
                  {text}
                </span>
              ))}
            </span>
          </p>
        </section>
      </div>
    </div>
  );
};

export default Author;
